import React, { useState, useEffect } from "react";
import s from './ProfileInfo.module.css';
import Preloader from '../../common/Preloader/Preloader';


const ProfileStatusWithHooks = (props) => {
    // let stateWithSetState = useState(false);// на выходе это массив с первым значением в массиве false, как мы указали
    // let editMode = stateWithSetState[0];// выбираем первей элемент в массиве - false
    // let setEditMode = stateWithSetState[1];// вторым элементом в массиве сидит функция которая меняет значения 
    // //1го элемента в массиве 
    //сокращенная версия кода выше
    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect(() => {
        setStatus(props.status);
    }, [props.status]); //useEffect будет запускаться каждый раз как будут изменения в props.status

    const activateEditMode = () => {
        setEditMode(true);
    };

    const deactivateEditMode = () => {
        setEditMode(false);
        props.updateStatus(status);
    };

    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value);
    };
    return (
        < div >
            {!editMode &&
                <div >
                    <span onDoubleClick={activateEditMode}> Статус: {status || '________'}</span>
                </div>}
            {editMode &&
                <div >
                    <input autoFocus={true} onBlur={deactivateEditMode} onChange={onStatusChange} value={status}></input>
                </div>}
        </div >
    );
}




export default ProfileStatusWithHooks 