import React from "react";
import s from './ProfileInfo.module.css';
import Preloader from '../../common/Preloader/Preloader';


class ProfileStatus extends React.Component {

    state = {
        editMode: false,
        status: this.props.status
    }
    activateEditMode = () => {

        this.setState({
            editMode: true
        });

    }
    deactivateEditMode = () => {
        this.setState({
            editMode: false
        });
        this.props.updateStatus(this.state.status);//диспатчим локальный статус в store и делаем
        //глобальный state.status равным значению локального
    };

    onStatusChange = (e) => {
        this.setState({
            status: e.currentTarget.value //меняем локальный статус на впечатанный юзером
        });
    };
    componentDidUpdate(prevState, prevProps) {
        if (prevProps.status !== this.props.status) {
            this.setState({
                status: this.props.status
            });
        }

    }
    render() {

        return (

            < div >
                {!this.state.editMode &&
                    <div >
                        <span onDoubleClick={this.activateEditMode}>Статус: {this.props.status || '________'}</span>
                    </div>}
                {this.state.editMode &&
                    <div>
                        <input onChange={this.onStatusChange} autoFocus={true} onBlur={this.deactivateEditMode} value={this.state.status}></input>
                    </div>}
            </div >
        );
    }
}



export default ProfileStatus 