import React from "react";
import s from './ProfileInfo.module.css';
import Preloader from '../../common/Preloader/Preloader';
import userPhoto from '../../../assets/images/user.png';
import ProfileStatus from './ProfileStatus';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = (props) => {
  if (!props.profile) {
    return <Preloader />
  }

  let userContacts = props.profile.contacts;


  let contact = [];
  for (var key in userContacts) {
    if (userContacts[key] != null) {
      contact.push(key + ' - ' + userContacts[key]);
    }
  }
  return (
    <div >
      {/* <div>
        <img alt="image" src='https://www.pixelstalk.net/wp-content/uploads/images6/HD-Wallpaper-Aesthetic-Macbook.jpg' />
      </div> */}
      <div className={s.descriptionBlock}>
        {/* props.profile.photos.large != null ? props.profile.photos.large : */}
        <img className={s.avatar} src={props.profile.photos.large != null ? props.profile.photos.large : userPhoto}></img>
        <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
        <div>
          <div><span>Имя: </span>{props.profile.fullName}</div>
          <div><span>Обо мне:</span> {props.profile.aboutMe}</div>
          <div className={s.job}>
            {props.profile.lookingForAJob ? <span >В поиске работы</span>
              : <span>{props.profile.lookingForAJobDescription}</span>}
          </div>
          <div>
            <span>Контакты:</span>
            {contact.map(c => {
              return <div>{c}</div>
            })}
          </div>

        </div>



      </div>
    </div>
  );
}


export default ProfileInfo