import React from "react";
import { addPostActionCreator } from "../../../redux/profile-reducer";
import MyPosts from './MyPosts';
import { connect } from "react-redux";


let mapStateToProps = (state) => {// connect автомостически в аргумент в 1й функции вкладывает state
  return {
    posts: state.profilePage.posts,
    newPostText: state.profilePage.newPostText

  };
};
let mapDispatchToProps = (dispatch) => {// connect автомостически в аргумент во второй
  //функции вызывает забайнденный dispatch из store
  return {
    addPost: (formData) => {
      dispatch(addPostActionCreator(formData));
    }
  };
};

let MyPostsContainer = connect(mapStateToProps, mapDispatchToProps)(MyPosts);


export default MyPostsContainer;  